const state = {
  accessToken: ''
};

const getters = {
  getAccessToken: state => state.accessToken
};

const mutations = {
  updateAccessToken(state, param) {
    state.accessToken = param;
  }
};

const actions = {
  updateAccessToken(context, accessToken) {
    context.commit('updateAccessToken', accessToken)
  }
};

export const authentication = {
  namespaced: true,
  state,
  actions,
  mutations,
  getters
};
